import React from 'react'
import Row from './Row'
import {Link, BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import WeaponsSvg from './background/weapons-svg'
import UtilitySvg from './background/utility-svg'
import CoreSvg from './background/core-svg'
import weaponComponents from '../data/weapon_components'
import utilityComponents from '../data/utility_components'
import coreComponents from '../data/core_components'
import './Style.css'

function App () {
  return (
    <Router>
      <header>
        <Link id='/ssc-r' className='link weapon-link' to='/ssc-r'>Weapon components</Link>
        <Link id='/utility' className='link utility-link' to='/utility'>Utility components</Link>
        <Link id='/core' className='link core-link' to='/core'>Core components</Link>
      </header>
      <div id='header-bottom'></div>
      <Switch>
        <Route exact path='/ssc-r'>
          <Row page={weaponComponents}>
            <WeaponsSvg/>
          </Row>
        </Route>
        <Route exact path='/utility'>
          <Row page={utilityComponents}>
            <UtilitySvg/>
          </Row>
        </Route>
        <Route exact path='/core'>
          <Row page={coreComponents}>
            <CoreSvg/>
          </Row>
        </Route>
      </Switch>
    </Router>
  )
}

export default App